import "./styles.css";

document.getElementById("qv-button").onclick = () => {
  window.parent.postMessage(
    {
      type: "embeddedAppAPIMessage",
      method: "navigateToQuickview",
      methodVersion: "1.0.0",
      params: { patientId: "5" }
    },
    "https://prodmirror.athenahealth.com"
  );
};
